* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fafafa;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-form {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  color: #212121;
  height: 40px;
  border: 1px solid #303030;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  max-height: 100vh;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
  overflow: auto;
}

.gallery-div {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform .25s;
}

.gallery-link + .info {
  opacity: 0;
}

.gallery-link:hover {
  cursor: zoom-in;
}

.gallery-link:hover + .info, .info:hover {
  opacity: 1;
}

.info {
  width: 100%;
  z-index: 1;
  background-color: rgba(39, 174, 96, .4);
  gap: 10px;
  padding-left: 15px;
  padding-right: 15px;
  transition: opacity .3s linear;
  display: flex;
  position: absolute;
  bottom: 0;
}

gallery-link {
  text-decoration: none;
}

